.btn {
	display: inline-block;
	border: 1px solid #025fff;
	background-color: transparent;
    border-radius: 4px;
	cursor: pointer;
	color: #343a40;
	font-family: inherit;
	font-weight: 400;
	text-align: center;
	line-height: 1.5;
	transition: all 0.2s;
}
.btn:hover {
	background-color: #357fff;
	color: #fff;
}
.btn:disabled {
    border: 1px solid rgba(0,0,0, .12);
    font-weight: 500;
    color: rgba(0,0,0, .3);
    background-color: rgba(255,255,255, .6);
    cursor: default;
}
.btn:disabled:hover {
    background-color: rgba(255,255,255, .6);
}

/* Primary */
.btn_primary {
	background-color: #025fff;
	border-color: #025fff;
	color: #fff;
}
.btn_primary:hover {
	background-color: #004cce;
}
.btn_outline_primary {
	border-color: #025fff;
	color: #025fff;
}
.btn_outline_primary:hover {
	background-color: #025fff;
}



