/* Sizes */
.small {
    padding: 4px 10px;
	font-size: 13.5px;
}
.medium {
    padding: 6px 16px;
	font-size: 14px;
}
.large {
    padding: 8px 22px;
	font-size: 14px;
}

.upperCase {
	text-transform: uppercase;
	font-weight: 500;
}